export default {
  data () {
    return {
      activeIndex: 0,
      tabList: [
        '全部',
        '待付款',
        '待收货',
        '退款/退货'
      ],
      orderList: []
    }
  },
  onLoad (options) {
    this.activeIndex = parseInt(options.activeIndex) || 0
    this.queryOrderList()
  },
  methods: {
    async queryOrderList () {
      let data = await this.$request({
        url: `/api/public/v1/my/orders/all?type=${this.activeIndex + 1}`,
        isAuth:true
      })
      this.orderList = data.orders
    },
    clickSearch (index) {
      this.activeIndex = index
      this.queryOrderList()
    }
  }
}
